import React from 'react';
import Button from 'react-bootstrap/Button';
import '../index.css'

const BodyDesign = (props) => {
        return (
            <>
            <div className="complete-body">
                <div className="body-design-cart">
                <Button variant="warning" size="lg" className="body-design-cart">
                    <img src='../images/cart.png' className="cart-img" alt="cart-logo"></img>Cart - {props.finalcount}
                    </Button>
                    <Button variant="danger" className="danger-button"  onClick={props.handleclick}>Reset</Button>
                </div> 
                <div className="body-design">
                    <h1>Shop in style</h1>
                    <p>With this Latest HighClass Products :)</p>
                </div>
            </div>
            </>
         );
    }
 
export default BodyDesign;