import { Card} from "react-bootstrap";
import '../index.css'

export default function Contact() {
    var variant = "dark"
    return (
        <>
                    <Card
                bg={variant.toLowerCase()}
                text={variant.toLowerCase() === 'light' ? 'dark' : 'white'}
                style={{ width: 'auto', margin: '20px', padding: '10px', textAlign: 'center' }}
                className="mb-2 contact-card"
            >
                <Card.Header>WELCOME</Card.Header>
                <Card.Body>
                <Card.Title className="cardTitle">Name: Ajay Kanakam</Card.Title>
                <Card.Title className="cardTitle">Age: 27</Card.Title>
                <Card.Title className="cardTitle">Locality: Indian</Card.Title>
                <Card.Title className="cardTitle">Designation: Lead Programmer</Card.Title>
                <hr/>
                <Card.Text>
                    In case of any feedback please reach me out @
                </Card.Text>
                <a href="#" className="hrefLinks">GMAIL</a> <br/><br/>
                <a href="#" className="hrefLinks">LINKED-IN</a>
                </Card.Body>
            </Card>
        </>
    )
}