import React, { Component } from 'react';
import { Card } from 'react-bootstrap';
import Button from 'react-bootstrap/Button';
import '../../node_modules/bootstrap/dist/css/bootstrap.css'
import '../index.css'
import BodyDesign from './BodyDesign';
import ReactStars from "react-rating-stars-component";


class PriceCart extends Component {
    constructor(props) {
        super(props);
        this.state = {
            cart_count: 0,
            Total: 0,
            arr: [
                {
                    id: 1,
                    img: './images/product1.jpg',
                    title: 'Product1',
                    count1: 0,
                    price: 299,
                    sale: 'Sale',
                    strickedprice: '$399',
                    value: 4,
                    disable: false
                },
                {
                    id: 2,
                    img: "./images/product2.jpg",
                    title: 'Product2',
                    count1: 0,
                    price: 300,
                    value: 4.5,
                    disable: false
                },
                {
                    id: 3,
                    img: "./images/product3.jpg",
                    title: 'Product3',
                    count1: 0,
                    price: 599,
                    sale: 'Sale',
                    strickedprice: '$799',
                    value: 4.5,
                    disable: false
                },
                {
                    id: 4,
                    img: "./images/product4.jpg",
                    title: 'Product4',
                    count1: 0,
                    price: 150,
                    value: 4,
                    disable: false
                },
                {
                    id: 5,
                    img: "./images/product5.jpg",
                    title: 'Product5',
                    count1: 0,
                    price: 100,
                    value: 3,
                    disable: false
                },
                {
                    id: 6,
                    img: "./images/product6.jpg",
                    title: 'Product6',
                    count1: 0,
                    price: 250,
                    value: 3.5,
                    disable: false
                },
                {
                    id: 7,
                    img: "./images/product7.jpg",
                    title: 'Product7',
                    count1: 0,
                    price: 199,
                    value: 3.5,
                    disable: false
                },
                {
                    id: 8,
                    img: "./images/product8.jpg",
                    title: 'Product8',
                    count1: 0,
                    price: 299,
                    sale: 'Sale',
                    value: 5,
                    strickedprice: '$599',
                    disable: false
                }
            ]
        }
    }

    handleclick = (x) => {
        let index = this.state.arr.findIndex(arr => arr.id === x.id);
        let items = [...this.state.arr]
        items[index].disable = true;
        items[index].count1 +=1;
        let totalAmt = 0
        totalAmt = totalAmt + items[index].price
        this.setState({
            cart_count: this.state.cart_count + 1,
            Total: this.state.Total + totalAmt,
            arr: items
        })
    }

    handleClickCart = () => {
        if (window.confirm("Do you want to delete Cart items")) {
        let itemsCart = [...this.state.arr]
        let length = this.state.arr.length;
        for (let x=0; x<length; x++) {
            itemsCart[x].disable = false;
        }
        this.setState({
            cart_count: 0,
            Total: 0,
            arr: itemsCart
        })
        }        
    }


    
    render() { 
        const ratingChanged = (newRating) => {
            console.log(newRating);
          };
        return ( 

            <>
               <BodyDesign finalcount={this.state.cart_count} finalTotal = {this.state.Total} handleclick = {this.handleClickCart}/>

                <h4 className="header4">Total Amount - ${this.state.Total}</h4>
               {/* <Button variant="warning">Cart {this.state.cart_count}</Button>
            <div className="body-design">
                <h1>Shop in style</h1>
                <p>With this shop hompeage template</p>
            </div> */}
            
                <div className="price-cart">
                {this.state.arr.map((x,index) => {
                    if (x.sale) {
                return <>
                <Card key={index} style={{ width: '18rem' }}>
                <div className='image-width'>
                    <img src={x.img} alt="product-img"/>
                </div>
                <Card.Body>
                    <Card.Title>{x.title}</Card.Title>
                    <Card.Text>Price : <s>{x.strickedprice}</s> ${x.price}</Card.Text>
                    <span className="span-tag">{x.sale}</span>
                    <ReactStars
                        count={5}
                        onChange={ratingChanged}
                        size={24}
                        isHalf = {true}
                        edit = {false}
                        value= {x.value}
                        activeColor="#ffd700"
                    /> <br/>
                    <Button variant="outline-dark" disabled={x.disable} onClick={() => this.handleclick(x)}>Add to Cart</Button>
                </Card.Body>
                </Card>
                </> } else {
                  return <>
                  <Card key={index} style={{ width: '18rem' }}>
                  <div className='image-width'>
                      <img src={x.img} alt="product-img"/>
                  </div>
                  <Card.Body>
                      <Card.Title>{x.title}</Card.Title>
                      <Card.Text>Price : <s>{x.strickedprice}</s> ${x.price}</Card.Text>
                      <ReactStars
                        count={5}
                        onChange={ratingChanged}
                        edit = {false}
                        value= {x.value}
                        isHalf = {true}
                        size={24}
                        activeColor="#ffd700"
                    />   <br/>                   
                      <Button variant="outline-dark" disabled={x.disable} onClick={() => this.handleclick(x)}>Add to Cart</Button>
                  </Card.Body>
                  </Card>
                  </>  
                }
        })}
        </div>
            </>
         );
    }
}
 
export default PriceCart;