import React, { Component } from 'react';
import img from '../images/Shopping.jpg'
import '../index.css'


class Home extends Component {

    render() { 
        return ( 
            <>
                <h1 className="h1-home">My Shopping Home</h1>
                <hr/>
                <div className="img-div">
                    <img src ={img} alt = "Logo" />
                </div>

            </>
         );
    }
}
 
export default Home;