import React, { Component } from 'react';
import PriceCart from './PriceCart';
import Footer from './Footer';
import '../index.css'

class FinalShopBody extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            count: 0
         }
    }

    handleclick = () => {
        this.setState({
            count: this.state.count + 1,
        })
    }    
    render() { 
 
              return ( 
            <>
                <PriceCart />
               <Footer />
            </>
         );
    }
}
 
export default FinalShopBody;

